<template>
  <div class="parent">
    <img
      src="../assets/images/ramadan-green.gif"
      class="image1"
      alt="Girl in a jacket"
    />
    <img v-bind:src="store.dp" class="image2" alt="Girl in a jacket" />
    <CIcon :icon="globeIcon" :height="35" class="globe-icon" />
    <img v-bind:src="alhosnPic" class="alhosn-logo" alt="Girl in a jacket" />
    <div class="name">{{ name }}</div>
    <div id="ppn" class="ppn" v-on:click="togglePpn()">
      PPN: {{ ppnString }}
    </div>
    <div class="dot">.</div>
    <div id="uid" class="uid" v-on:click="toggleUid()">
      UID: {{ uidString }}
    </div>
    <div id="dob" class="dob" v-on:click="toggleDob()">
      DoB: {{ dobString }}
    </div>
  </div>
</template>

<script>
import dp from '@/assets/images/hisham.jpg'
import alhosnPic from '@/assets/images/alhosn-pic.png'
import infoPic from '@/assets/images/info.png'
import { store } from './store.js'
export default {
  name: 'TopBar',
  props: {
    msg: String,
  },
  setup() {
    const globeIcon = 'cil-globe-alt'
    const infoIcon = 'cil-info'
    return {
      globeIcon,
      infoIcon,
    }
  },
  data() {
    return {
      dp: dp,
      alhosnPic: alhosnPic,
      infoPic: infoPic,
      name: store.name, // 'SAYED HISHAM SAYYED JAFAR VALIYA MALIYAKKAL',
      ppn: store.ppn, // 'V4597776',
      uid: store.uid, // '70081510',
      dob: '',
      dobString: '',
      ppnString: '',
      uidString: '',
      store,
    }
  },
  methods: {
    toggleDob() {
      if (this.dobString.includes('**')) {
        this.dobString = store.dob
      } else {
        this.dobString = '*********'
      }
    },
    togglePpn() {
      if (this.ppnString.includes('**')) {
        this.ppnString = this.ppn
      } else {
        this.ppnString = this.ppn.substring(0, 4) + '****'
      }
    },
    toggleUid() {
      if (this.uidString.includes('**')) {
        this.uidString = this.uid
      } else {
        this.uidString = this.uid.substring(0, 4) + '****'
      }
    },
    realDobString() {
      var dobMonth = this.dob.toLocaleString('default', { month: 'short' })
      var dobString =
        ('0' + this.dob.getDate()).slice(-2) +
        ' ' +
        //   ('0' + (this.dob.getMonth() + 1)).slice(-2) +
        dobMonth +
        ' ' +
        this.dob.getFullYear()
      return dobString
    },
  },
  mounted() {
    // var dobMonth = this.dob.toLocaleString('default', { month: 'short' })
    // this.dobString =
    //   ('0' + this.dob.getDate()).slice(-2) +
    //   ' ' +
    //   //   ('0' + (this.dob.getMonth() + 1)).slice(-2) +
    //   dobMonth +
    //   ' ' +
    //   this.dob.getFullYear()
    this.ppnString = this.ppn.substring(0, 4) + '****'
    this.uidString = this.uid.substring(0, 4) + '****'
    this.dobString = '*********'
    console.log(this.dobString)
  },
}
</script>

