<script>
import dp from '@/assets/images/hisham.jpg'
import alhosnPic from '@/assets/images/alhosn-pic.png'
import infoPic from '@/assets/images/info.png'
import { store } from './store.js'
export default {
  name: 'TopBar',
  props: {
    msg: String,
  },
  setup() {
    const globeIcon = 'cil-globe-alt'
    return {
      globeIcon,
    }
  },
  data() {
