  methods: {
    changeDurationType() {
      // console.log(document.getElementById('durationType').value)
      store.durationType = document.getElementById('durationType').value
      // this.$set(store.durationType, 'val', e)
    },
    onFileChange(e) {
      var files = e.target.files || e.dataTransfer.files
      if (!files.length) return
      store.dp = URL.createObjectURL(files[0])
      console.log(store.dp)
    },
    submit() {
      store.showCard = true
    },
  },
  mounted() {},
}
