<template>
  <div class="qr-details">
    <div class="result">
      {{ store.duration }} {{ store.durationType }} - {{ store.result }}
    </div>
    <div class="result-date">
      {{ durationString }}
    </div>
    <div class="rainbow">
      <img v-bind:src="qr" class="qr-pic" alt="Girl in a jacket" />
    </div>
    <div class="refresh-text">
      QR code will be updated in {{ store.updateTime }}
    </div>
  </div>
</template>

<script>
import { store } from './store.js'
import qrPic from '@/assets/images/qr.jpg'
import alhosnPic from '@/assets/images/alhosn-pic.png'
export default {
  name: 'QrDetails',
  props: {
    msg: String,
  },
  setup() {
    const globeIcon = 'cil-globe-alt'
    return {
      globeIcon,
    }
  },
  data() {
    return {
      durationString: 'Since ',
      qr: qrPic,
      alhosnPic: alhosnPic,
      updateTime: 'aa',
      store,
    }
  },
  methods: {
    startTimer(duration) {
      var timer = duration,
        minutes,
        seconds
      setInterval(function () {
        minutes = parseInt(timer / 60, 10)
        seconds = parseInt(timer % 60, 10)

        minutes = minutes < 10 ? '0' + minutes : minutes
        seconds = seconds < 10 ? '0' + seconds : seconds

        store.updateTime = minutes + ':' + seconds
        // console.log(this.updateTime)
        if (--timer < 0) {
          store.showCard = true
        }
      }, 1000)
    },
    calculateDate() {
      console.log(this.qr)
      var date = new Date()
      var month = date.toLocaleString('default', { month: 'short' })
      if (store.durationType == 'hours') {
        date.setHours(date.getHours() + store.duration)
        this.durationString +=
          ('0' + date.getDate()).slice(-2) +
          ' ' +
          month +
          ' ' +
          date.getFullYear()
      }
    },
  },
  mounted() {
    this.startTimer(60 * 5)
    console.log(this.qr)
    this.calculateDate()
  },
}
</script>

